import React, { useState, useEffect } from "react";
import styled from "styled-components";
import logo from "../assets/logodelta.png";
import { GiHamburgerMenu } from "react-icons/gi";
import { VscChromeClose } from "react-icons/vsc";
import Scrollspy from 'react-scrollspy'
export default function Navbar() {

  const [navbarState, setNavbarState] = useState(false);


  return (
    <>
      <Nav>
        <div className="brand">
          <div className="container">
            <Img src={logo} alt="" />
            Delta Bank
          </div>
          <div className="toggle">
            {navbarState ? (
              <VscChromeClose onClick={() => setNavbarState(false)} />
            ) : (
              <GiHamburgerMenu onClick={() => setNavbarState(true)} />
            )}
          </div>
        </div>        
      <Scrollspy
        className="scrollspy" items={ ['home', 'services', 'recommended', 'testomonials'] }
            currentClassName="active" >

        <li><a href="#home">Home</a></li>
        <li><a href="#services">Services</a></li>
        <li><a href="#mission">Our Mission</a></li>
        <li><a href="#join">Join Today</a></li>

      </Scrollspy>
      </Nav>
      <ResponsiveNav state={navbarState}>
        <ul>
          <li>
            <a href="#home" onClick={() => setNavbarState(false)}>
              Home
            </a>
          </li>
          <li>
            <a href="#services" onClick={() => setNavbarState(false)}>
              Services
            </a>
          </li>
          <li>
            <a href="#mission" onClick={() => setNavbarState(false)}>
              Our Mission
            </a>
          </li>
          <li>
            <a href="#join" onClick={() => setNavbarState(false)}>
              Join Today
            </a>
          </li>
        </ul>
      </ResponsiveNav>
    </>
  );
}

const Nav = styled.nav`
  position: -webkit-sticky; /* Safari */
	position: sticky;
	top: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 95%;
  padding-left: 5%;
  padding-top: 10px;
  padding-bottom: 10px;
  background-color: #fff;
  .active {
    color: #2b2d45;
    text-decoration: underline;
  }
  .brand {
    .container {
      cursor: pointer;
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 0.4rem;
      font-size: 1.2rem;
      font-weight: 900;
      text-transform: uppercase;
    }
    .toggle {
      display: none;
    }
  }
  ul {
    display: flex;
    gap: 1rem;
    list-style-type: none;
    li {
      a {
        text-decoration: none;
        font-size: 1.2rem;
        transition: 0.1s ease-in-out;
        color: #2b2d45;
        &:hover {
          color: #023e8a;
        }
      }
      &:first-of-type {
        a {
          color: #023e8a;
          font-weight: 900;
        }
      }
    }
  }
  button {
    padding: 0.5rem 1rem;
    cursor: pointer;
    border-radius: 1rem;
    border: none;
    color: white;
    background-color: #48cae4;
    font-size: 1.1rem;
    letter-spacing: 0.1rem;
    text-transform: uppercase;
    transition: 0.3s ease-in-out;
    &:hover {
      background-color: #023e8a;
    }
  }
  @media screen and (min-width: 280px) and (max-width: 1080px) {
    .brand {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      .toggle {
        display: block;
      }
    }
    ul {
      display: none;
    }
    button {
      display: none;
    }
  }
`;

const ResponsiveNav = styled.div`
  position: -webkit-sticky; /* Safari */
	position: sticky;
	top: 0;
  display: flex;
  position: absolute;
  z-index: 1;
  top: ${({ state }) => (state ? "50px" : "-400px")};
  background-color: white;
  height: 30vh;
  width: 100%;
  align-items: center;
  transition: 0.3s ease-in-out;
  ul {
    list-style-type: none;
    width: 100%;
    li {
      width: 100%;
      margin: 1rem 0;
      margin-left: 2rem;
      a {
        text-decoration: none;
        font-size: 1.2rem;
        transition: 0.1s ease-in-out;
        &:hover {
          color: #023e8a;
        }
      }
      }
    }
  }
`;

const Img = styled.img`
  height: 40px;
  width: auto;
`